import React, { useEffect, useRef, useState, useMemo } from 'react';
import styled from 'styled-components';
import { Flex } from 'reflexbox/styled-components';
import CreditCardInput from 'react-credit-card-input';
import Card from 'react-credit-cards';
import 'react-credit-cards/es/styles-compiled.css';
import * as validate from 'card-validator';

function useDialogCloseHandler(cb) {
  const nodeRef = useRef();
  useEffect(() => {
    const { current: el } = nodeRef;
    const onEvent = event => {
      const { target, key } = event;
      if (key && key.toLowerCase() !== 'escape') {
        return;
      }
      if (target === el || el.contains(target)) {
        return;
      }
      cb();
    };
    document.addEventListener('mousedown', onEvent, true);
    document.addEventListener('keydown', onEvent, true);
    return () => {
      document.removeEventListener('mousedown', onEvent, true);
      document.removeEventListener('keydown', onEvent, true);
    };
  }, [cb]);
  return { ref: nodeRef };
}

const Dialog = styled.dialog`
  top: 0;
  bottom: 0;
  border: none;
  background-color: ${props => props.theme.palette.body};
  color: ${props => props.theme.palette.text};
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.59);
`;

function Modal({ open, onClose, children, className }) {
  const handleOutsideClick = useDialogCloseHandler(onClose);
  return (
    <Dialog {...handleOutsideClick} className={className} open={open}>
      <Flex
        height="100%"
        width="100%"
        p="5px"
        flexDirection="column"
        alignItems="center"
      >
        {children}
      </Flex>
    </Dialog>
  );
}

const PayModal = styled(Modal)`
  height: 370px;
`;

const Input = styled.input.attrs({ type: 'text' })`
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  border: none;
  border-radius: 3px;
  font-size: 20px;
  margin: 10px 0;
  padding: 5px;
`;

function useEventDrivenState() {
  const [state, setState] = useState('');
  const change = e => {
    setState(e.target.value);
  };
  return [state, change];
}

const PurchaseButton = styled.button`
  padding: 20px;
  margin-top: 10px;
  border-radius: 10px;
  font-size: 2em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
  border: none;

  &[disabled] {
    opacity: 0.4;
  }
`;

export function PayDialog({ open, onClose, onBill }) {
  const [name, setName] = useEventDrivenState();
  const [ccNumber, setCcNumber] = useEventDrivenState();
  const [expiry, setExpiry] = useEventDrivenState();
  const [cvc, setCVC] = useEventDrivenState();
  const isValid = useMemo(() => {
    return (
      name.split(' ').length > 1 &&
      validate.number(ccNumber).isValid &&
      validate.expirationDate(expiry).isValid &&
      validate.cvv(cvc).isValid
    );
  }, [name, ccNumber, expiry, cvc]);

  const inputRef = useRef(null);
  useEffect(() => {
    if (open) {
      inputRef.current.focus();
    }
  }, []);

  return (
    <PayModal open={open} onClose={onClose}>
      <Card
        number={ccNumber}
        name={name}
        expiry={expiry.replace(/\s/g, '')}
        cvc={cvc}
      />
      <Input
        placeholder="Card holder name"
        value={name}
        onChange={setName}
        ref={inputRef}
      />
      <CreditCardInput
        cardNumberInputProps={{
          value: ccNumber,
          onChange: setCcNumber
        }}
        cardExpiryInputProps={{
          value: expiry,
          onChange: setExpiry
        }}
        cardCVCInputProps={{
          value: cvc,
          onChange: setCVC
        }}
        fieldClassName="input"
      />
      <PurchaseButton
        disabled={!isValid}
        onClick={() => {
          onBill();
          onClose();
        }}
      >
        Top up!
      </PurchaseButton>
    </PayModal>
  );
}
