import React, { createContext, useContext, useState } from 'react';
import axios from 'axios';

import { HOST } from './config';

const UserContext = createContext();
const useUser = () => useContext(UserContext);

function UserProvider({ children }) {
  const [ userState, setUserState ] = useState(userLocalStorageManagement.get());

  function setUser(userData) {
    if (!userData) return;
    setUserState(userData);
    userLocalStorageManagement.set(userData);
  }

  async function fetchUser(username, password) {
    const { data: userData } = await axios.get(HOST + '/api/me', {
      params: { username, password }
    });
    setUser(userData);
  }

  async function updateUserBalance(gameName, delta, description) {
    const gamesRoutes = {
      classicSlotMachine: '/games/classic-slot-machine',
      advancedSlotMachine: '/games/advanced-slot-machine'
    };
    const { token } = userLocalStorageManagement.get();
    const { data: userData } = await axios({
      url: HOST + '/api/me/bills',
      method: 'POST',
      data: { delta, description, url: gamesRoutes[gameName] },
      headers: {
        'x-token': token
      }
    });
    setUser(userData);
  }

  function logoutUser() {
    setUserState(null);
    userLocalStorageManagement.remove();
  }

  const contextData = {
    user: userState,
    fetchUser,
    updateUserBalance,
    logoutUser
  };

  return <UserContext.Provider value={contextData}>{children}</UserContext.Provider>;
}

const USER_LOCAL_STORAGE_KEY = '//clover/user/v2';

const userLocalStorageManagement = {
  set(user) {
    localStorage.setItem(USER_LOCAL_STORAGE_KEY, JSON.stringify(user));
  },
  get() {
    const user = localStorage.getItem(USER_LOCAL_STORAGE_KEY);
    return user && JSON.parse(user);
  },
  remove() {
    localStorage.removeItem(USER_LOCAL_STORAGE_KEY);
  }
};

export default UserProvider;
export { useUser };
