import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { Flex, Box } from 'reflexbox/styled-components';

import slotMachine from '../../images/slot-machine.jpg';
import bigSlotMachine from '../../images/big-slot-machine.jpg';

const Card = styled(Flex).attrs({
  px: '10px',
  py: '20px',
  alignItems: 'center',
  flexDirection: 'column'
})`
  background-color: ${(props) => props.theme.palette.inverse};
  color: ${(props) => props.theme.palette.inverseText};
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.59);
  text-align: center;
`;

const GameImage = styled.img`
  width: 200px;
  height: 200px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${(props) => props.theme.palette.primary};
  mix-blend-mode: ${(props) => props.blendMode || 'unset'};
  object-position: ${(props) => props.position || 'initial'};
`;

const PlayButton = styled(Link)`
  display: block;
  padding: 20px;
  border-radius: 10px;
  font-size: 2em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${(props) => props.theme.palette.primary};
  color: ${(props) => props.theme.palette.primaryText};
  text-decoration: none;
`;

function GamesPage() {
  return (
    <Flex justifyContent="center" alignItems="center" height="100%" width="100%">
      <Card mx="50px" width="300px" height="600px">
        <GameImage src={slotMachine} alt="Classic slot machine image" blendMode="multiply" />
        <h1>Slot Machine</h1>
        <Box flex={1}>
          <h3>
            Only two things are infinite, <br />
            the universe and slot machine combos, <br />
            and I'm not sure about the former.
          </h3>
        </Box>
        <PlayButton to="/games/classic-slot-machine">Play!</PlayButton>
      </Card>
      <Card mx="50px" width="300px" height="600px">
        <GameImage src={bigSlotMachine} alt="Advanced slot machine image" blendMode="multiply" position="center 20px" />
        <h1>Advanced Slot Machine</h1>
        <Box flex={1}>
          <h3>The bigger the risk, the greater the reward!</h3>
        </Box>
        <PlayButton to="/games/advanced-slot-machine" style={{ backgroundColor: '#2db14b' }}>
          Play!
        </PlayButton>
      </Card>
    </Flex>
  );
}

export default GamesPage;
