import React from 'react';
import styled from 'styled-components';
import { LinkButton } from 'alex-clover-ui';

import {
  classicSMdescriptionHtml,
  advancedSMdescriptionHtml
} from './slotMachinesDescriptions';
import classicSlotMachineImage from '../../../images/slot-machine.jpg';
import advancedSlotMachineImage from '../../../images/big-slot-machine.jpg';

export default function SlotMachineAbout({
  slotMachineGameName,
  linkUrl,
  cardWidth
}) {
  const imgSrc =
    slotMachineGameName === 'Classic'
      ? classicSlotMachineImage
      : advancedSlotMachineImage;

  const gameDescriptionHtml =
    slotMachineGameName === 'Classic'
      ? classicSMdescriptionHtml
      : advancedSMdescriptionHtml;

  return (
    <Container>
      <Card width={cardWidth}>
        <GameImage
          src={imgSrc}
          alt={`${slotMachineGameName} slot machine image`}
          blendMode="multiply"
          position="center 20px"
        />
        <FlexColumn>
          <h3>{slotMachineGameName} Slot Machine</h3>
          {gameDescriptionHtml}
        </FlexColumn>
      </Card>
      <LinkButton
        linkUrl={linkUrl}
        buttonText="Play now!"
        fontSize="6em"
        padding="50px"
      />
    </Container>
  );
}

const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
`;

const Container = styled(FlexColumn)`
  align-items: center;
  margin-bottom: 50px;
`;

const Card = styled.div`
  box-sizing: border-box;
  width: ${(props) => props.width};
  display: flex;
  align-items: center;
  padding: 20px;
  margin-bottom: 50px;
  background-color: ${(props) => props.theme.palette.inverse};
  color: ${(props) => props.theme.palette.inverseText};
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.59);
`;

const GameImage = styled.img`
  width: 200px;
  min-width: 200px;
  height: 200px;
  min-height: 200px;
  border: 5px solid ${(props) => props.theme.palette.primary};
  border-radius: 100%;
  margin-right: 10px;
  object-fit: cover;
  mix-blend-mode: ${(props) => props.blendMode || 'unset'};
  object-position: ${(props) => props.position || 'initial'};
`;
