import React, { useState, useRef, useEffect, Fragment } from 'react';
import styled from 'styled-components';

import { getResults } from '../../../ajaxRequests';
import { useUser } from '../../../UserProvider';

import SlotMachineColumn from './SlotMachineColumn';

export default function SlotMachine({ slotMachineType }) {
  // ------- Slot Machine games configurations ------- //
  const gamesNames = {
    classic: 'classicSlotMachine',
    advanced: 'advancedSlotMachine'
  };
  const slotsAmount = {
    classic: 3,
    advanced: 5
  };
  const gamePrice = {
    classic: 150,
    advanced: 250
  };
  const gamePriceDescription = {
    classic: 'Playing Classic Slot Machine game',
    advanced: 'Playing Advanced Slot Machine game'
  };
  const winningPrize = {
    classic: 500,
    advanced: 1500
  };
  const winningPrizeDescription = {
    classic: 'Winning at Classic Slot Machine!',
    advanced: 'Winning at Advanced Slot Machine!'
  };
  const enableDiagonalWin = {
    classic: false,
    advanced: true
  };
  // -------------------------------------------------- //

  const gameResults = useRef();
  const machines = useRef(Array(slotsAmount[slotMachineType]));
  const isSlotMachineSpinning = useRef(false);
  const winningSpritesDirection = useRef('');

  const [ shouldUseWinningStyles, setShouldUseWinningStyles ] = useState(false);
  const [ winningSpriteNum, setWinningSpriteNum ] = useState(null);

  function initializeColumnsWinningStateArr(slotsAmount) {
    const columnsWinningStateArr = [];
    for (let i = 0; i < slotsAmount; i++) {
      columnsWinningStateArr.push({ doesContainWinningSprite: false });
    }
    return columnsWinningStateArr;
  }

  const [ columnsWinningState, setColumnsWinningState ] = useState(
    // Array(slotsAmount[slotMachineType]).fill({
    //   doesContainWinningSprite: false
    // })
    initializeColumnsWinningStateArr(slotsAmount[slotMachineType])
  );

  const { updateUserBalance } = useUser();

  useEffect(
    () => {
      setColumnsWinningState(
        // Array(slotsAmount[slotMachineType]).fill({
        //   doesContainWinningSprite: false
        // })
        initializeColumnsWinningStateArr(slotsAmount[slotMachineType])
      );
    },
    [ slotMachineType ]
  );

  // ---------------- Helper functions ----------------//
  function createGetNextSpriteNumFunction(index) {
    return () => gameResults.current.resultsArr[index];
  }

  function getNextSpriteNum(currentSpriteNum) {
    return currentSpriteNum < 7 ? currentSpriteNum + 1 : 0;
  }

  function getPreviousSpriteNum(currentSpriteNum) {
    return currentSpriteNum > 0 ? currentSpriteNum - 1 : 7;
  }

  function calculateWinningSpriteNum() {
    const { resultsArr } = gameResults.current;

    if (slotMachineType === 'classic') {
      winningSpritesDirection.current = 'horizontal';
      return resultsArr[0];
    }

    if (slotMachineType === 'advanced') {
      let isHorizontalWinning = true,
        isTopLeftToBottomRightDiagonalWinning,
        isBottomLeftToTopRightDiagonalWinning;

      for (let i = 0; i < resultsArr.length - 1; i++) {
        if (resultsArr[i] !== resultsArr[i + 1]) {
          isHorizontalWinning = false;
          break;
        }
      }

      if (isHorizontalWinning) {
        winningSpritesDirection.current = 'horizontal';
        return resultsArr[0];
      }

      for (let i = 0; i < resultsArr.length - 2; i++) {
        // Checking top-left to bottom-right diagonals
        isTopLeftToBottomRightDiagonalWinning =
          getPreviousSpriteNum(resultsArr[i]) === resultsArr[i + 1] &&
          resultsArr[i + 1] === getNextSpriteNum(resultsArr[i + 2]);
        // Checking bottom-left to top-right diagonals
        isBottomLeftToTopRightDiagonalWinning =
          getNextSpriteNum(resultsArr[i]) === resultsArr[i + 1] &&
          resultsArr[i + 1] === getPreviousSpriteNum(resultsArr[i + 2]);

        if (
          isTopLeftToBottomRightDiagonalWinning ||
          isBottomLeftToTopRightDiagonalWinning
        ) {
          winningSpritesDirection.current = 'diagonal';
          return resultsArr[i + 1];
        }
      }
    }
  }

  function setWinningSpriteColumns(winningSpriteNum) {
    let columnsWinningStateArr;
    if (winningSpritesDirection.current === 'horizontal') {
      columnsWinningStateArr = columnsWinningState.map(() => {
        return { doesContainWinningSprite: true };
      });
    } else if (winningSpritesDirection.current === 'diagonal') {
      columnsWinningStateArr = [ ...columnsWinningState ];

      const { resultsArr } = gameResults.current;
      for (let i = 0; i < resultsArr.length - 2; i++) {
        if (
          (getPreviousSpriteNum(resultsArr[i]) === winningSpriteNum &&
            resultsArr[i + 1] === winningSpriteNum &&
            getNextSpriteNum(resultsArr[i + 2]) === winningSpriteNum) ||
          (getNextSpriteNum(resultsArr[i]) === winningSpriteNum &&
            resultsArr[i + 1] === winningSpriteNum &&
            getPreviousSpriteNum(resultsArr[i + 2]) === winningSpriteNum)
        ) {          
          columnsWinningStateArr[i].doesContainWinningSprite = true;
          columnsWinningStateArr[i + 1].doesContainWinningSprite = true;
          columnsWinningStateArr[i + 2].doesContainWinningSprite = true;
          break;
        }
      }
    }
    setColumnsWinningState(columnsWinningStateArr);
  }
  // -------------------------------------------------- //

  async function handleClick() {
    if (isSlotMachineSpinning.current) {
      return;
    }
    setShouldUseWinningStyles(false);
    setWinningSpriteNum(null);
    isSlotMachineSpinning.current = true;

    // 1) Bill user for playing the game
    await updateUserBalance(
      gamesNames[slotMachineType],
      gamePrice[slotMachineType],
      gamePriceDescription[slotMachineType]
    );

    // 2) Get game results from server
    gameResults.current = await getResults(
      slotsAmount[slotMachineType],
      enableDiagonalWin[slotMachineType]
    );

    // 3) Spin the slot machine columns and and stop on sprite number that returned from server
    await Promise.all(
      machines.current.map(
        (machine) => new Promise((resolve) => machine.shuffle(resolve))
      )
    );

    // 4) In case user won, add styles to the relevant sprites and give user the prize
    if (gameResults.current.didUserWin) {
      const winningSpriteNum = calculateWinningSpriteNum();
      setWinningSpriteNum(winningSpriteNum);
      setWinningSpriteColumns(winningSpriteNum);
      setShouldUseWinningStyles(true);
      updateUserBalance(
        gamesNames[slotMachineType],
        -winningPrize[slotMachineType],
        winningPrizeDescription[slotMachineType]
      );
    }

    isSlotMachineSpinning.current = false;
  }

  return (
    <MainContainer>
      <Fragment>
        {columnsWinningState.map((column, index) => (
          <SlotMachineColumn
            key={slotMachineType + '-' + index}
            getNextSpriteNum={createGetNextSpriteNumFunction(index)}
            setSlotMachineColumnRef={(machine) =>
              (machines.current[index] = machine)}
            shouldUseWinningStyles={shouldUseWinningStyles}
            doesContainWinningSprite={column.doesContainWinningSprite}
            winningSpriteNum={winningSpriteNum}
          />
        ))}
      </Fragment>
      <StyledButton onClick={handleClick}>Roll!</StyledButton>
    </MainContainer>
  );
}

// --------------- Styled Components ----------------- //

const MainContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const StyledButton = styled.button`
  width: 100px;
  height: 100px;
  position: relative;
  margin-left: 30px;
  color: var(--primaryText);
  background-color: var(--primary);
  cursor: pointer;
  border: none;
  border-radius: 100%;
  font-size: 2.5em;
  font-weight: bold;
`;
