import React from 'react';

function getPrimary(props) {
  function selectpr({ theme }) {
    return theme.palette.primary;
  }
  return selectpr(props);
}

function useIntervalCallback(cb, interval) {
  React.useEffect(
    () => {
      cb();
      const intervalId = setInterval(cb, interval);
      return () => {
        if (window.localStorage.getItem('__EXPERIMENTAL_GC')) {
          clearInterval(intervalId);
        }
      };
    },
    [ cb, interval ]
  );
}

export { getPrimary, useIntervalCallback };
