import React from 'react';
import styled from 'styled-components';
import { LinkButton } from 'alex-clover-ui';

import SlotMachineAbout from '../games/SlotMachine/SlotMachineAbout';

import casino from '../../images/casino.jpeg';

const TopContainer = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(${casino});
  background-repeat: no-repeat;
`;

const BottomContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const MegaTitle = styled.h1`
  font-size: 8em;
  text-shadow: 1px 1px 20px black;
  margin-bottom: 0;
`;

const Title = styled.h2`
  font-size: 4em;
  text-shadow: 1px 1px 10px black;
  a {
    display: block;
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.4);
    color: rgba(45, 177, 75, 1);
    border-radius: 5px;
  }
`;

function WelcomePage() {
  return (
    <>
      <TopContainer>
        <MegaTitle>
          <span role="img" aria-label="clover leave">
            üçÄ
          </span>{' '}
          Clover Casino
        </MegaTitle>
        <Title>
          <a href="#game-list">Take a look at our Games</a>
        </Title>
        <LinkButton
          linkUrl="/games"
          buttonText="Play now!"
          fontSize="6em"
          padding="50px"
        />
      </TopContainer>
      <BottomContainer id="game-list">
        <Title>
          Take a look at our Clover Games<sup>TM</sup>
        </Title>
        <SlotMachineAbout
          slotMachineGameName="Classic"
          linkUrl="/games/classic-slot-machine"
          cardWidth="80%"
        />
        <SlotMachineAbout
          slotMachineGameName="Advanced"
          linkUrl="/games/advanced-slot-machine"
          cardWidth="80%"
        />
      </BottomContainer>
    </>
  );
}

export default WelcomePage;
