import React, { useState } from 'react';
import styled from 'styled-components';
import { Redirect } from 'react-router-dom';
import { Flex, Box } from 'reflexbox/styled-components';
import { TextInput } from 'alex-clover-ui';

import { useUser } from '../../UserProvider';

const LoginBox = styled(Box).attrs({ width: '500px' })`
  background-color: ${props => props.theme.palette.secondary};
  box-shadow: 1px 1px 5px black;
`;

const LoginButton = styled.button`
  padding: 20px;
  font-size: 2em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  border: none;
  &:disabled {
    opacity: 0.3;
  }
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
`;

const Error = styled.p`
  text-align: center;
  color: ${props => props.theme.palette.negative};
`;

const createChangeHandler = setter => e => setter(e.target.value);

function LoginPage() {
  const [hasError, setHasError] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const { user, fetchUser } = useUser();

  function submit(e) {
    e.preventDefault();
    if (username && password) {
      try {
        setHasError(false);
        fetchUser(username, password);
      } catch (e) {
        setHasError(true);
      }
    }
  }

  if (user) {
    return <Redirect to="/games" />;
  }
  return (
    <Flex
      height="100%"
      width="100%"
      alignItems="center"
      justifyContent="center"
    >
      <LoginBox>
        <LoginForm onSubmit={submit}>
          <Box flex={1} px="50px" pt="50px">
            <TextInput
              id="username"
              shouldBeFocused={true}
              labelText="Username"
              type="text"
              name="username"
              value={username}
              onChangeHandler={createChangeHandler(setUsername)}
              fontSize="1.5em"
            />
          </Box>
          <Box px="50px" py="30px" flex={1}>
            <TextInput
              id="password"
              labelText="Password"
              type="password"
              name="password"
              value={password}
              onChangeHandler={createChangeHandler(setPassword)}
              fontSize="1.5em"
            />
          </Box>
          {hasError && <Error>Bad username or password</Error>}
          <LoginButton id="btn" disabled={!(username + password)}>
            Login
          </LoginButton>
        </LoginForm>
      </LoginBox>
    </Flex>
  );
}

export default LoginPage;
