import axios from 'axios';

import { HOST } from './config';

export async function getResults(slotsAmount, enableDiagonalWin) {
  const { data } = await axios({
    method: 'POST',
    url: HOST + '/api/slot/result',
    data: { slots: slotsAmount, enableDiagonalWin }
  });
  const gameResults = {
    resultsArr: data.results,

    // Horizontal
    // resultsArr: [ 4, 4, 4 ],
    // resultsArr: [ 5, 5, 5, 5, 5 ],

    // resultsArr: [1, 0, 7, 0, 0],
    // resultsArr: [ 0, 7, 6, 0, 0 ],
    // resultsArr: [ 3, 0, 7, 6, 0 ],

    // Top-left to bottom-right diagonals
    // resultsArr: [ 2, 1, 0, 5, 1 ],
    // resultsArr: [ 6, 2, 1, 0, 5 ],
    // resultsArr: [ 5, 6, 2, 1, 0 ],

    // Bottom-left to top-right diagonals
    // resultsArr: [ 0, 1, 2, 1, 6 ],
    // resultsArr: [ 6, 0, 1, 2, 5 ],
    // resultsArr: [ 5, 6, 0, 1, 2 ],

    // Both diagonals
    // resultsArr: [ 2, 1, 0, 1, 2 ],
    // resultsArr: [ 0, 1, 2, 1, 0 ],

    // didUserWin: true
    didUserWin: data.winningResults
  };
  return gameResults;
}
