import React from 'react';
import { Switch, Route } from 'react-router-dom';
import styled from 'styled-components';

import { GamesSidebar } from './GamesSidebar';
import SlotMachine from './SlotMachine/SlotMachine';
import SlotMachineAbout from './SlotMachine/SlotMachineAbout';
import NotFoundPage from '../pages/NotFoundPage';

import { useUser } from '../../UserProvider';

function Games() {
  const { user } = useUser();
  return (
    <Container>
      <GamesSidebar />
      <Switch>
        <Route
          path="/games/classic-slot-machine"
          render={() =>
            user ? (
              <SlotMachine slotMachineType="classic" />
            ) : (
              <SlotMachineAbout
                slotMachineGameName="Classic"
                linkUrl="/login"
                cardWidth="100%"
              />
            )}
        />
        <Route
          path="/games/advanced-slot-machine"
          render={() =>
            user ? (
              <SlotMachine slotMachineType="advanced" />
            ) : (
              <SlotMachineAbout
                slotMachineGameName="Advanced"
                linkUrl="/login"
                cardWidth="100%"
              />
            )}
        />
        <Route component={NotFoundPage} />
      </Switch>
    </Container>
  );
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default Games;
