import React, { useRef, useEffect } from 'react';
import SlotMachineColumnCreater from 'jquery-slotmachine/lib/slot-machine';
import styled, { css, keyframes } from 'styled-components';

import slotMachineSpritesImg from '../../../images/slot-sprites.png';

export default function SlotMachineColumn({
  getNextSpriteNum,
  setSlotMachineColumnRef,
  shouldUseWinningStyles,
  doesContainWinningSprite,
  winningSpriteNum
}) {
  const slotMachineElementRef = useRef();

  useEffect(() => {
    const element = slotMachineElementRef.current;
    const slotMachine = new SlotMachineColumnCreater(element, {
      active: 0,
      spins: 10,
      delay: 450,
      randomize: getNextSpriteNum
    });
    setSlotMachineColumnRef(slotMachine);
  }, []);

  const imagesNamesArr = [
    'bell',
    'cherry',
    'grape',
    'seven',
    'gold',
    'bar',
    'two bar',
    'three bar'
  ];

  return (
    <ColumnContainer ref={slotMachineElementRef}>
      {imagesNamesArr.map((imageName, i) => (
        <StyledImage
          key={i}
          src={slotMachineSpritesImg}
          alt={imageName}
          id={i}
          shouldUseWinningStyles={
            shouldUseWinningStyles &&
            doesContainWinningSprite &&
            winningSpriteNum === i
          }
        />
      ))}
    </ColumnContainer>
  );
}

// --------------- Styled Components ----------------- //

const ColumnContainer = styled.div`
  width: 100px;
  height: 300px;
  display: flex;
  flex-direction: column;
`;

const blinkAura = keyframes`
  0% {
    box-shadow: 0px 0px 2px var(--primary) inset;
  }
  100% {
    box-shadow: 0px 0px 15px var(--primary) inset;
  }
`;
const animation = props =>
  css`
    ${blinkAura} 0.6s ease-out 0s infinite alternate none running;
  `;

const StyledImage = styled.img`
  width: 100px;
  height: 110px;
  object-fit: none;
  transform: translateY(100px);
  border-radius: 5px;
  object-position: ${props => `${-10 - 110 * props.id}px -80px`};

  animation: ${props => (props.shouldUseWinningStyles ? animation : '')};
`;

