import React, { useState, useCallback } from "react";
import styled, { css } from "styled-components";
import Gravatar from "react-gravatar";
import axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import { Flex } from "reflexbox/styled-components";
import { useUser } from "../../UserProvider";
import { Loading } from "../Loading";
import { getPrimary } from "../../utils";
import { CurrentBalance } from "../CurrentBalance";
import { PayDialog } from "../PayDialog";
import { useIntervalCallback } from "../../utils";
import { HOST } from "../../config";

const GREEN = "#2db14b";

const Card = styled(Flex).attrs({
  alignItems: "center",
  flexDirection: "column"
})`
  background-color: ${({
    theme: {
      palette: { secondary: backgroundColor }
    }
  }) => backgroundColor};
  color: ${({
    theme: {
      palette: { secondaryText: inverse }
    }
  }) => inverse};
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.59);
  text-align: center;
`;

const UserInfoCard = styled(Card)`
  width: 370px;
  @media (max-width: 1400px) {
    width: 300px;
  }
  @media (max-width: 1024px) {
    width: 300px;
  }
`;

const BillingHistoryCard = styled(Card)`
  width: 950px;
  height: 100%;
  @media (max-width: 1400px) {
    width: 850px;
  }
  @media (max-width: 1024px) {
    width: 650px;
  }
`;

const Page = styled.div`
  height: 100%;
  width: 100%;
  padding: 50px 20px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
`;

const Avatar = styled(Gravatar).attrs({ size: 150 })`
  border-radius: 100%;
  border: 5px solid ${props => getPrimary(props)};
`;

let [positive, negative] = ["positive", "negative"];
const balanceColor = css`
  color: ${props =>
    props.theme.palette[parseInt(props.children) > 0 ? positive : negative]};
`;

const Balance = styled.big`
  font-size: 3em;
  font-weight: bold;
  width: 100%;
  margin-bottom: 20px;
  ${balanceColor}
`;

const PurchaseButton = styled.button`
  padding: 20px;
  border-radius: 10px;
  font-size: 1.8em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.color};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
  border: none;
`;

const PrimaryButton = styled.button`
  cursor: pointer;
  padding: 5px;
  border-radius: 3px;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  border: none;
  height: fit-content;
`;

const TopBar = styled(Flex).attrs({
  as: "header",
  width: "100%",
  height: "50px",
  justifyContent: "space-between",
  p: "10px"
})`
  background-color: ${props => props.theme.palette.primary};
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.59);
`;

function UserPage() {
  const [showShareDialog, setShowShareDialog] = useState(false);
  const [showBillingDialog, setShowBillingDialog] = useState(false);
  const { user, updateUserBalance, logoutUser } = useUser();
  if (!user) {
    return null;
  } else {
    return (
      <Page>
        <PayDialog
          open={showBillingDialog}
          onClose={() => setShowBillingDialog(false)}
          onBill={() => updateUserBalance(-200, "Top up from credit")}
        />
        <UserInfoCard mr="20px" mb="20px" px="10px" py="20px">
          <Avatar email={user.email} />
          <h1>{user.username}</h1>
          <h3>{user.email}</h3>
          <Balance>{user.balance}$</Balance>
          <PurchaseButton
            color={GREEN}
            onClick={() => {
              setShowBillingDialog(true);
            }}
          >
            Buy more tokens!
          </PurchaseButton>
        </UserInfoCard>
        <BillingHistoryCard>
          <TopBar>
            <PrimaryButton onClick={() => setShowShareDialog(true)}>
              Share current balance
            </PrimaryButton>
            <PrimaryButton onClick={logoutUser}>Logout</PrimaryButton>
          </TopBar>
          <CurrentBalance
            value={`My current balance at Clover is: ${user.balance}`}
            open={showShareDialog}
            onClose={() => setShowShareDialog(false)}
          />
          <BillingHistory />
        </BillingHistoryCard>
      </Page>
    );
  }
}

const Table = styled.table`
  border-collapse: collapse;
  th {
    position: sticky;
    top: 0;
    background-color: ${props => props.theme.palette.secondary};
    padding-top: 10px;
  }
  tfoot {
    td {
      position: sticky;
      bottom: 0;
      background-color: ${props => props.theme.palette.secondary};
    }
  }
  tbody {
    tr {
      height: 30px;
      &:nth-child(even) {
        background-color: rgba(255, 255, 255, 0.1);
      }

      td {
        border-spacing: 0;
      }
    }
  }
`;

const BillLink = styled(Link)`
  text-decoration: none;
  color: ${props => props.theme.palette.primary};
`;

const BalanceText = styled.span`
  ${balanceColor}
`;

const TotalBalanceText = styled(BalanceText)`
  margin-left: 40px;
  font-size: 1.5em;
`;

const EnlargedText = styled.span`
  font-size: 1.5em;
`;

const TimeAgo = styled.span`
  text-indent: 10px;
  display: inline-block;
  opacity: 0.4;
`;

function BillingHistory() {
  const [billing, setBilling] = useState(null);
  const {
    user: { token }
  } = useUser();
  const fetchBilling = useCallback(
    () =>
      axios(HOST + "/api/me/bills", {
        headers: {
          "x-token": token
        }
      }).then(({ data }) => setBilling(data.reverse())),
    [token]
  );
  useIntervalCallback(fetchBilling, 10000);
  if (!billing) {
    return (
      <Flex
        flex={1}
        alignItems="center"
        justifyContent="center"
        height="100%"
        width="100%"
      >
        <Loading />
      </Flex>
    );
  }
  return (
    <Flex flex={1} width="100%" style={{ overflowY: "auto" }}>
      <Table>
        <thead>
          <tr>
            <th>Description</th>
            <th>Amount</th>
            <th>Balance</th>
            <th>Time of billing</th>
          </tr>
        </thead>
        <tbody>
          {billing.map((bill, idx) => (
            <tr key={idx}>
              <td>
                {bill.url ? (
                  <BillLink to={bill.url}>{bill.description}</BillLink>
                ) : (
                  bill.description
                )}
              </td>
              <td>
                <BalanceText>{-bill.delta}</BalanceText>
              </td>
              <td>
                <BalanceText>{bill.balance}</BalanceText>
              </td>
              <td>
                {moment(bill.timestamp).format("dddd, MMMM Do YYYY, h:mm:ss a")}
                <TimeAgo>({moment(bill.timestamp).fromNow()})</TimeAgo>
              </td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr>
            <td />
            <td colSpan={3}>
              <Flex
                width="100%"
                alignItems="center"
                height="20px"
                px="25px"
                py="5px"
                my="10px"
              >
                <EnlargedText>Total</EnlargedText>
                <TotalBalanceText>
                  {billing.reduce((sum, { delta }) => sum - delta, 0)}
                </TotalBalanceText>
              </Flex>
            </td>
          </tr>
        </tfoot>
      </Table>
    </Flex>
  );
}

export default UserPage;
