import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import Gravatar from 'react-gravatar';
import { HeaderLogo } from 'alex-clover-ui';

import { useUser } from '../UserProvider';

const HeaderContainer = styled.header`
  display: flex;
  justify-content: space-between;
  height: 50px;
  padding: 0 10px;
  background-color: ${(props) => props.theme.palette.primary};
`;

const StyledLink = styled(Link)`
  display: flex;
  align-items: center;
  color: ${(props) => props.theme.palette.primaryText};
  text-decoration: none;
`;

const StyledSpan = styled.span`margin-right: 10px;`;

function HeaderAccountData({ user }) {
  if (!user) {
    return null;
  }
  return (
    <StyledLink to="/me">
      <StyledSpan>{user.username}</StyledSpan>
      <Gravatar size={30} email={user.email} />
    </StyledLink>
  );
}

function Header() {
  const { user } = useUser();
  const route = user ? '/games' : '/';
  const logoIcon = (
    <span role="img" aria-label="clover-icon">
      üçÄ
    </span>
  );
  return (
    <HeaderContainer>
      <HeaderLogo
        brandName="clover"
        iconAriaLabel="clover leave icon"
        icon={logoIcon}
        route={route}
        fontSize="2em"
        spaceBetweenIconAndText="5px"
      />
      <HeaderAccountData user={user} />
    </HeaderContainer>
  );
}

export default Header;
