import React from 'react';
import styled from 'styled-components';
import { Route, Link } from 'react-router-dom';
import { Flex, Box } from 'reflexbox/styled-components';
import { useUser } from '../../UserProvider';
import slotMachine from '../../images/slot-machine.jpg';
import bigSlotMachine from '../../images/big-slot-machine.jpg';

const Sidebar = styled(Flex).attrs({
  as: 'aside',
  width: '200px',
  height: '100%',
  p: '10px',
  flexDirection: 'column'
})`
  background-color: ${(props) => props.theme.palette.secondary};
  color: ${(props) => props.theme.palette.secondaryText};

  hr {
    background-color: currentColor;
    border: none;
    width: 100%;
    height: 1px;
  }
`;

const BalanceText = styled.h3`
  margin-top: 0;
  margin-bottom: 0.5em;
  color: ${(props) => props.theme.palette[parseInt(props.children) > 0 ? 'positive' : 'negative']};
`;

const GameImage = styled.img`
  width: 100px;
  height: 100px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${(props) => props.theme.palette.primary};
  mix-blend-mode: ${(props) => props.blendMode || 'unset'};
  object-position: ${(props) => props.position || 'initial'};
`;

function renderChild(slotMachine, url, altText, shouldReposition) {
  return (
    <Box my="10px" alignSelf="center">
      <Link to={url}>
        <GameImage src={slotMachine} alt={altText} position={shouldReposition && 'center 5px'} />
      </Link>
    </Box>
  );
}

export function GamesSidebar() {
  const { user } = useUser();
  if (!user) return null;
  return (
    <Sidebar>
      <h2>Balance</h2>
      <BalanceText>{user.balance}$</BalanceText>
      <hr />
      <Route
        path="/games/classic-slot-machine"
        children={({ match }) => {
          return match ? null : renderChild(slotMachine, '/games/classic-slot-machine', 'Classic slot machine image');
        }}
      />
      <Route
        path="/games/advanced-slot-machine"
        children={({ match }) => {
          return match
            ? null
            : renderChild(bigSlotMachine, '/games/advanced-slot-machine', 'Advanced slot machine image', true);
        }}
      />
    </Sidebar>
  );
}
